.easter__egg:hover span:nth-child(1) {
  transition-delay: .8s;
  transform: translate(-50%, -130%);
}

.easter__egg:hover span:nth-child(2) {
  transition-delay: .8s;
  transform: translate(-50%, 30%);
}

.easter__egg:hover span:nth-child(3) {
  opacity: 1;
  transition: opacity .7s .8s;
}

.easter__egg span:nth-child(1), .easter__egg span:nth-child(2) {
  color: #545454;
  width: 100%;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

.easter__egg span:nth-child(1) {
  clip-path: polygon(0 0, 100% 0%, 100% 50%, 0 50%);
}

.easter__egg span:nth-child(2) {
  clip-path: polygon(0 50%, 100% 50%, 100% 100%, 0 100%);
}

.easter__egg span:nth-child(3) {
  text-transform: uppercase;
  opacity: 0;
  color: #fff;
  width: 97.5%;
  text-align: center;
  letter-spacing: 7px;
  word-spacing: .6rem;
  background: #ff8445;
  padding-left: 5px;
  font-size: 8px;
  line-height: 100%;
  transition: opacity .35s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

