.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: hidden;
  opacity: 0;
  z-index: 20;
  background: rgba(0, 0, 0, .2);
  transition: visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.active {
  visibility: visible;
  opacity: 1;
}

.modal {
  width: 280px;
  max-height: calc(100vh - 20px);
  visibility: hidden;
  opacity: 0;
  z-index: 30;
  background-color: #fff;
  padding: 40px 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal.active {
