  animation: blur-text 1.5s linear .6s infinite alternate;
}

.loading-text span:nth-child(5) {
  filter: blur();
  animation: blur-text 1.5s linear .8s infinite alternate;
}

.loading-text span:nth-child(6) {
  filter: blur();
  animation: blur-text 1.5s linear 1s infinite alternate;
}

